html,
body {
  margin: 0;
  font-family: monospace;
  font-weight: 700;
  font-size: 18px;
  cursor: default;
}
#container {
  display: flex;
  flex-direction: column;
  position: absolute;
  overflow: auto;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.page {
  display: flex;
  justify-content: center;
}

.ingredient { 
    margin: 0 calc(1ex / 0.32) 0 0;
}
 a {

    margin: 0;
    color: #1976d2;
    -webkit-text-decoration: none;
    text-decoration: none;
}
a:hover {
    -webkit-text-decoration: underline;
    text-decoration: underline;
}
.image-container {
    width: 100%;
    height: 325px;
}
img {
    margin-left: auto;
    margin-right: auto;
}

section { 
    height: 100%
}
article {
    display: flex;
    height: 100%;
    height: 100vh;
    justify-content: center;
}